.singleimage {
  position: relative;
}
.singleimage img {
  display: block;
  max-width: 100%;
}
.singleimage .text {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: imagetextbackground;
  color: imagetextcolor;
  justify-content: flex-end;
  align-items: center;
}
.slideshow .slide {
  position: relative;
  width: 100%;
}
.slideshow .slide img {
  display: block;
  max-width: 100%;
}
.slideshow .text {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: gutter;
  background: imagetextbackground;
  color: #fff;
}
.slideshow .flickity-prev-next-button,
.slideshow .flickity-page-dots {
  opacity: 0;
  transition: opacity 300ms linear;
}
.slideshow:hover .flickity-prev-next-button,
.slideshow:hover .flickity-page-dots {
  opacity: 1;
}
.slideshow:hover .flickity-prev-next-button[disabled],
.slideshow:hover .flickity-page-dots[disabled] {
  opacity: 0.3;
}
.slideshow .flickity-page-dots {
  bottom: 1rem;
}
.slideshow .flickity-page-dots .dot {
  background-color: ci_grey;
  opacity: 1;
}
.slideshow .flickity-page-dots .is-selected {
  background-color: #fff;
}
.wot {
  background: wotbackground;
  padding-bottom: gutter;
}
.wot .section:not(:last-child) {
  border-bottom: 1px solid maincolor;
  margin-bottom: gutter;
  padding-bottom: gutter;
}
.wot big {
  color: maincolor;
  display: block;
  padding: 1rem;
}
.halfcolumn {
  padding-top: 1rem;
  padding-bottom: 1rem;
}
.halfcolumn img {
  max-width: 100%;
}
.lshape {
  background: wotbackground;
  padding-bottom: 1rem;
}
.presentation {
  background: presentationbackground;
}
.presentation * {
  color: presentationcolor;
}
.presentation .people {
  lost-utility: clearfix;
}
.presentation .people .person {
  text-align: center;
  margin-bottom: 2em;
}
.presentation .people .img {
  width: 80%;
  left: 10%;
  position: relative;
  padding-bottom: 80%;
  border-radius: 50%;
  overflow: hidden;
  background: personimagebackground;
  margin-bottom: 0.5em;
}
.presentation .people .img img {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.presentation .people .name {
  white-space: nowrap;
  margin-bottom: 0.5em;
}
.presentation .people .position {
  font-style: italic;
}
.publications {
  padding-bottom: 1rem;
}
.publications .text {
  color: maincolor;
}
.newsteaser {
  lost-utility: clearfix;
  padding-top: 1em;
  padding-bottom: 1em;
}
.newsteaser .text {
  padding-bottom: 1rem;
}
.newsteaser .text big,
.newsteaser .text p {
  margin-bottom: 2rem;
}
.newsteaser .button {
  background-color: ci_green;
  color: #000;
  text-decoration: none;
  padding: 0.8rem 1.6rem;
  display: inline-block;
}
.newsteaser .newspreviews {
  min-height: remify(300px);
  padding-bottom: 7rem;
}
.newsteaser .newspreviews .flickity-page-dots {
  bottom: 4rem;
}
.newsteaser .newspreviews .newsarticle {
  lost-column: 1/1 0;
  text-decoration: none;
  color: inherit;
  display: block;
}
.newsteaser .newspreviews .newsarticle img {
  max-width: 100%;
}
.newsteaser .newspreviews .subtitle {
  color: ci_grey;
}
.activities .row {
  lost-utility: clearfix;
}
.activities .img img {
  max-width: 100%;
}
.activities .intro,
.activities .phases {
  padding-top: 1rem;
  padding-bottom: 1rem;
}
.activities .intro {
  background-color: ci_grey_light;
}
.activities .phases h3 {
  margin: 0 0 1rem;
}
.activities .phases h3 .name {
  color: #000;
}
.activities .phase {
  margin-bottom: 3rem;
}
.network {
  padding-top: 1rem;
  padding-bottom: 1rem;
}
.network .inner .partner {
  display: block;
  padding-bottom: 2rem;
  padding-top: 1rem;
}
.network .inner .partner img {
  max-width: 80%;
  margin-top: 1rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.network .inner .partner h3 a:link {
  text-decoration: none;
}
.network .inner .partner h3 a:link,
.network .inner .partner h3 a:visited,
.network .inner .partner h3 a:hover,
.network .inner .partner h3 a:active {
  color: maincolor;
}
.network .inner .partner h3 a:hover {
  border-bottom: 0.1rem solid maincolor;
}
.network .inner .partner + .partner {
  margin-top: 1rem;
  border-top: 1px solid maincolor;
}
.getinvolved .inner {
  padding-top: 0;
}
.getinvolved .inner .maintitle {
  background: wotbackground;
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.getinvolved .inner .maintitle h2 {
  color: maincolor;
}
.getinvolved .inner .lasttext {
  background: wotbackground;
  padding-top: 2rem;
  padding-bottom: 3rem;
}
.getinvolved .inner .contacts .contactform {
  margin-bottom: 3rem;
  margin-top: 2rem;
}
.getinvolved .inner .contacts .contactform h3 {
  color: #000;
}
.getinvolved .inner .contacts .contactform .wwww h4 {
  color: maincolor;
  margin-bottom: 0;
}
.getinvolved .inner .contacts .contactform .linkcontact {
  margin-top: 3rem;
}
.getinvolved .inner .contacts .contactform .linkcontact .button {
  background-color: #cbd70b;
  color: #000;
  text-decoration: none;
  padding: 0.8rem 1.6rem;
  display: inline-block;
}
.getinvolved .inner .contacts .contactform + .contactform {
  border-top: 1px solid maincolor;
}
.contactus .inner .content {
  margin-bottom: 3rem;
}
.contactus .inner .content .contactmodule {
  border-top: 1px solid maincolor;
  padding-top: 2rem;
  margin-top: 3rem;
}
.contactus .inner .content .contactmodule iframe {
  width: 99%;
  height: 420px;
  border: none;
}
